/* cyrillic */
@font-face {
  font-display: swap;
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 300;
  src: local('Roboto Condensed Light'), local('RobotoCondensed-Light'),
      url(/skin/frontend/adaptive/default/fonts/roboto/roboto-condensed-light-cyr.woff2) format('woff2'),
      url(/skin/frontend/adaptive/default/fonts/roboto/roboto-condensed-light.woff) format('woff');
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}
