html {
  font-size: 16px;
  position: relative;
  height: 100%;
}
@media (min-width: 768px) {
  html {
    font-size: 16px;
  }
}
body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  body {
    padding-top: 52px;
  }
}
.page-main {
  min-height: 350px;
  margin-bottom: 35px;
  padding-top: 20px;
  flex: 1 0 auto;
  position: relative;
}
@media (min-width: 1024px) {
  .page-main {
    margin-bottom: 50px;
  }
}
.page-main .breadcrumb {
  margin-top: -10px;
  margin-bottom: 0;
  margin-left: -10px;
  margin-right: -10px;
  padding: 0 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #D6EFFB;
}
@media (min-width: 768px) {
  .page-main .breadcrumb {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 10px;
    padding: 0;
    border-bottom: none;
  }
}
.catalog-product-view .page-main {
  background-color: #f4fbfe;
}
@media (min-width: 768px) {
  .catalog-product-view .page-main {
    background-color: #fff;
  }
}
.catalog-product-view .breadcrumb > li:last-child {
  display: none;
}
.category-discounts {
  background-color: #fff;
}
@media (min-width: 788px) and (max-width: 1023px) {
